import { useContext } from 'react';
import { ScrapeContext } from './scrapeContext';

const Data = () => {
  const { scrapes } = useContext(ScrapeContext)
  return (<div>
    <h2>Top 100 Profiles sorted by number of Followers</h2>
    <table>
      <thead>
        <tr>
          <td>InstagramId</td>
          <td>Name</td>
          <td>Number of followers</td>
          <td>Dreister Follower?</td>
        </tr>
      </thead>
      <tbody>
        {scrapes.map(scrape => (
          <tr key={scrape.instagramId}>
            <td>{scrape.instagramId}</td>
            <td>{scrape.name}</td>
            <td>{scrape.nbFollowers}</td>
            <td>{scrape.isFollowingDreister ? 'YES' : 'NO'}</td>
          </tr>
        ))}
      </tbody>
    </table>
  </div>)
}

export default Data;
